.video {
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%; 
    min-height: 100%;
}

#canvas {
    position: fixed;
    right: 0;
    bottom: 0;
    *min-width: 100%; 
    min-height: 100%;    
    z-index: 1;
}

.shotButton{
    position: fixed;
    bottom: 10px;
    border-radius: 50%;
    width: 40px;
    height:40px;
    left: 50%;
    z-index: 2;
}